import Vue from 'vue'
import rest from '../../assets/rest'
import {mock} from '../../assets/rest'
import * as api from '../../assets/api-consts';

Vue.$ioc.register('http',rest);
Vue.$ioc.register('api',api);
Vue.$ioc.register('mock',mock);
Vue.$ioc.register('const/mock_active',true);

export default 'rest';


