<template>
    <div v-if="visible" class="custom-messages alert" :class="[visible ? 'show' : '', messageType]" role="alert">
    	<!-- <button type="button" class="close" data-dismiss="alert" aria-label="Close"><span aria-hidden="true"></span></button> -->
        <span v-html="message"> </span>
    </div>
</template>

<script>
	import jQuery from 'jquery'
	import { alert } from 'bootstrap'

	export default {
	    props:{  message:String,messageType:{default:'alert-warning'}  },
        computed:{
	        visible(){
                return !(this.message==null || this.message=='');
            }
        }
	}

</script>


<style lang="scss" >
.custom-messages{
	position: absolute;
	right: 10px;
	top: 10px;
	margin-left: -3000px;
	transition: all .2s ease-out ;
	&.show{
		margin-right: 0;
		transition: all .2s ease-in;
	}
}
</style>


