import Vue from 'vue'
//import test from './pages/users-list/UserList'

const Home = r=>require.ensure([], () => r(require('./pages/Home')), 'user-view');
const UserList = r=>require.ensure([], () => r(require('./pages/users-list/UserList')), 'user-view');

export default{
    name:'home',
    path: '/home',
    component: Home,
    children:[
        {  path:'/home',  component: UserList }
    ]
};


