import axios from 'axios'
import MockAdapter  from 'axios-mock-adapter'
/**
 * @var String pathName
 * @type {string}
 */
let pathName =  window.location.pathname;
pathName = pathName.replace("AbsFrequentGuest","absfrequentguest").replace("/client","");

let baseURL= pathName;

if(process.env.NODE_ENV == 'development')
    baseURL = 'http://servicehub.abs-one.stg/absfrequentguest/res/01/0';

if( window.location.hostname=="localhost" ){ 
    baseURL = 'http://servicehub.abs-one.stg/absfrequentguest/res/01/0';
}

const instance  = axios.create({
    baseURL,
    timeout: 90000,
    headers: {
        'Content-Type': 'application/json'
    },
    withCredentials: true
});

export default instance;
export const rest = instance;
export const mock = new MockAdapter(instance,{ delayResponse: 1000 });


