import {state,mutations,actions} from './startup/store'
import guest from '../guest/store'
import portal_user from '../portal_user/store'
import reservation from '../reservation/store'
import Vue from 'vue'
import vuexI18n from 'vuex-i18n';
import Vuex from 'vuex'

Vue.use(Vuex);

guest.namespaced= true;
reservation.namespaced= true;
portal_user.namespaced= true;

const store = new Vuex.Store({
    state,
    mutations,
    actions,
    modules: { i18n: vuexI18n.store ,guest,reservation,portal_user }
});

export default store;


